import { Link ,useNavigate} from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";

import { useContext } from "react";
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Snackbar from '@mui/material/Snackbar';

import Box from '@mui/material/Box';

import API from "./API";


import noteContext from './NoteContext';

function Home() {
    const [data, setData] = useState([]);
    const [error, setError] = useState("%");
    const [loadingbar, setHideLoading] = useState(false);

    const [student_class, setClass] = useState(1);
    const [student_name, setStudentName] = useState();
    const navigate = useNavigate();
    const a = useContext(noteContext);
    let token = a.token;
    const fetchAPIData = async () => {

        try {
            const res = await API.get("students/" + student_class + "/" + student_name, {
                headers: {
                    Accept: 'application/json',
                    Authorization: 'Bearer ' + token
                }
            });
            setData(res.data);

            setHideLoading(true);
        }
        catch (error) {
            setError(error);

        }


    };

    useEffect(() => {

        if (a.login_status === null)
        navigate("/login")


    else {

        fetchAPIData();


    }


    
    }, []);






    if (data === undefined) return <> </>;


    if (!loadingbar)


        return (


            "Loading... please wait "
        )


    return (

        <div className="sakshmb">


            <div className="mb-3">



                <Box
                    component="form"
                    sx={{
                        '& > :not(style)': { m: 1, width: '25ch' },
                    }}
                    noValidate
                    autoComplete="off"
                >




                    <TextField
                        className="mr5"
                        required

                        label="Type Class"
                        id="type_class"

                        onChange={(e) => setClass(e.target.value)}


                        autoFocus
                    />





                    <TextField
                        className="mr5"
                        required

                        label="Student Name"
                        id="type_studentname"

                        value={student_name}

                        onChange={(e) => setStudentName(e.target.value)}



                    />




                    <Button onClick={fetchAPIData} variant="contained">Search Students</Button>

                </Box>
            </div>

            <hr >
            </hr>








            <table className="table  table-auto  ">


                <tbody>

                    {data.map((item, index) => (


                        <tr key={index}>
                            <td>   {item.sr} </td>

                            <td>   {item.name} </td>

                            <td>   {item.father_name} </td>

                            <td>   {item.mother_name} </td>


                            <td>

                                <Link className="btn btn-outline-success" to={`student/${item.sr}`}>View</Link>



                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>


        </div>

    )
}

export default Home;